@import '../../themes/default/modules/calendar.variables';

@if $use-calendar {
  /*******************************
  //@-        Popup
  *******************************/

  .ui.calendar .ui.popup {
    max-width: none;
    padding: 0;
    border: none;
    user-select: none;
  }

  /*******************************
              Calendar
  *******************************/

  .ui.calendar .calendar:focus {
    outline: 0;
  }

  /*******************************
              Grid
  *******************************/

  .ui.calendar .ui.popup .ui.grid {
    display: block;
    white-space: nowrap;
  }

  .ui.calendar .ui.popup .ui.grid > .column {
    width: auto;
  }

  /*******************************
              Table
  *******************************/

  .ui.calendar .ui.table.year,
  .ui.calendar .ui.table.month,
  .ui.calendar .ui.table.minute {
    min-width: 15em;
  }

  .ui.calendar .ui.table.day {
    min-width: 18em;
  }

  .ui.calendar .ui.table.day.andweek {
    min-width: 22em;
  }

  .ui.calendar .ui.table.hour {
    min-width: 20em;
  }

  .ui.calendar .ui.table tr th,
  .ui.calendar .ui.table tr td {
    padding: 0.5em;
    white-space: nowrap;
  }

  .ui.calendar .ui.table tr th {
    border-left: none;
  }

  .ui.calendar .ui.table tr th .icon {
    margin: 0;
  }

  .ui.calendar .ui.table tr:first-child th {
    position: relative;
    padding-left: 0;
    padding-right: 0;
  }

  .ui.calendar .ui.table.day tr:first-child th {
    border: none;
  }

  .ui.calendar .ui.table.day tr:nth-child(2) th {
    padding-top: 0.2em;
    padding-bottom: 0.3em;
  }

  .ui.calendar .ui.table tr td {
    padding-left: 0.1em;
    padding-right: 0.1em;
  }

  .ui.calendar .ui.table tr .link {
    cursor: pointer;
  }

  .ui.calendar .ui.table tr .prev.link {
    width: 14.28571429%;
    position: absolute;
    left: 0;
  }

  .ui.calendar .ui.table tr .next.link {
    width: 14.28571429%;
    position: absolute;
    right: 0;
  }

  .ui.calendar .ui.table tr .disabled {
    pointer-events: auto;
    cursor: default;
    color: $disabled-text-color;
  }

  .ui.calendar .ui.table tr .adjacent:not(.disabled) {
    color: $calendar-adjacent-text-color;
    background: $calendar-adjacent-background;
  }

  /*--------------
      States
  ---------------*/

  .ui.calendar .ui.table tr td.today {
    font-weight: $calendar-today-font-weight;
  }

  .ui.calendar .ui.table tr td.range {
    background: $calendar-range-background;
    color: $calendar-range-text-color;
    box-shadow: $calendar-range-box-shadow;
  }

  .ui.calendar .ui.table.inverted tr td.range {
    background: $calendar-range-inverted-background;
    color: $calendar-range-inverted-text-color;
    box-shadow: $calendar-range-inverted-box-shadow;
  }

  .ui.calendar:not(.disabled) .calendar:focus .ui.table tbody tr td.focus,
  .ui.calendar:not(.disabled) .calendar.active .ui.table tbody tr td.focus {
    box-shadow: $calendar-focus-box-shadow;
  }

  .ui.calendar:not(.disabled) .calendar:focus .ui.table.inverted tbody tr td.focus,
  .ui.calendar:not(.disabled) .calendar.active .ui.table.inverted tbody tr td.focus {
    box-shadow: $calendar-focus-inverted-box-shadow;
  }


  /*******************************
              States
  *******************************/

  /*--------------------
          Disabled
  ---------------------*/

  .ui.disabled.calendar {
    opacity: $disabled-opacity;
  }

  .ui.disabled.calendar > .input,
  .ui.disabled.calendar .ui.table tr .link {
    pointer-events: none;
  }
}

@import '../../themes/default/modules/calendar.style';